.components-progress {
  margin-top:10px;
  width: 100%;
  height: 3px;
  background: #90b2dd;
  cursor: pointer;
}
.components-progress:hover {
  -webkit-box-shadow: 0 1px 13px #272727;
          box-shadow: 0 1px 13px #272727;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
.components-progress .progress {
  width: 20%;
  height: 3px;
  background: #2f9842;
  margin-top: 40px;
  -webkit-transition: all .1s;
  -o-transition: all .1s;
  transition: all .1s;
  border-radius: 2px;
}



